.competitions {
  color: #ffffff;
  padding-bottom: 60px;
  position: relative;
  z-index: 1;
  display: flex;
  padding-top: 119px;
}

.competitions__wrapper {
  background: #ed0233;
  width: 43.1%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 6.7%;
  padding-right: 6.7%;
  padding-top: 83px;
}

.competitions__header {
  font-family: "BebasNeue-Bold", "Arial", sans-serif;
  font-size: 90px;
  line-height: 90px;
  letter-spacing: 0.03em;
  padding: 0;
  margin: 0;
  width: 201px;
}

.competitions__wrap {
  display: flex;
  flex-direction: column;
  width: 92px;
  margin-top: -2px;
}

.competitions__text {
  font-family: "RobotoCondensed-Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  padding: 0;
  margin: 0;

  &--time {
    margin-top: 8px;
  }

  &--advert {
    padding-top: 8px;
    padding-bottom: 47px;
  }
}

.competitions__value {
  font-family: "RobotoCondensed-Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 150%;
  padding: 0;
  margin: 0;
  margin-top: -1px;
  display: flex;
}

.competitions__value span {
  font-family: "RobotoCondensed-Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 70px;
  padding-top: 10px;
  padding-left: 5px;
}

.competitions__button {
  font-family: "RobotoCondensed-Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  padding: 0;
  margin: 0;
  margin-bottom: 86px;
  background-color: transparent;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  border: 4px solid #ffffff;
  padding-top: 24px;
  padding-bottom: 23px;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
}

.competitions__image {
  height: 100%;
}

@media (max-width: $tablet-width) {
  .competitions {
    padding-bottom: 40px;
    position: relative;
    padding-top: 100px;
    justify-content: space-between;
    max-width: 786px;
  }

  .competitions__wrapper {
    padding-top: 50px;
    max-width: 400px;
    width: calc(100% - 386px);
  }

  .competitions__header {
    font-size: 52px;
    line-height: 52px;
    width: 117px;
  }

  .competitions__wrap {
    width: 56px;
    margin-top: -2px;
  }

  .competitions__text {
    font-size: 12px;

    &--time {
      margin-top: -1px;
    }

    &--advert {
      padding-top: 0;
      padding-bottom: 29px;
    }
  }

  .competitions__value {
    font-size: 24px;
    line-height: 150%;
    margin-top: -1px;
    display: flex;
  }

  .competitions__value span {
    font-size: 10px;
    width: 50px;
    padding-top: 2px;
    padding-left: 5px;
  }

  .competitions__button {
    font-size: 10px;
    line-height: 12px;
    margin-bottom: 48px;
    border: 2px solid #ffffff;
    padding-top: 14px;
    padding-bottom: 13px;
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
  }

  .competitions__image {
    height: 100%;
    width: 386px;
  }
}

@media (max-width: $mobile-width) {
  .competitions {
    padding-bottom: 30px;
    position: relative;
    flex-direction: column;
    padding-top: 69px;
  }

  .competitions__wrapper {
    width: 100%;
    max-width: 442px;
    margin: 0 auto;
    padding-top: 48px;
    // padding-left: 14.9%;
    // padding-right: 14.9%;
    padding-left: 44px;
    padding-right: 44px;
  }

  .competitions__header {
    font-size: 52px;
    line-height: 52px;
    width: 117px;
  }

  .competitions__wrap {
    width: 57px;
    margin-top: -2px;
  }

  .competitions__text {
    &--time {
      margin-top: -1px;
    }

    &--advert {
      padding-top: 0;
      padding-bottom: 31px;
    }
  }

  .competitions__value {
    font-size: 24px;
    line-height: 150%;
    margin-top: -1px;
    display: flex;
  }

  .competitions__value span {
    width: 45px;
    padding-top: 4px;
  }

  .competitions__button {
    margin-bottom: 50px;
    padding-top: 13px;
    padding-bottom: 12px;
    width: 100%;
  }

  .competitions__image {
    // width: 290px;
    // height: 292px;
    margin: 0 auto;
    width: 100%;
    height: 100%;
    max-width: 442px;
    max-height: 445px;
  }
}
